.container {
   width: 1180px;
   margin: 150px auto;
   flex-wrap: wrap;
   justify-content: center;
 }

.completed {
   color: rgb(133, 128, 128);
   text-decoration: line-through;
}

.span {
    font-size: 10px;
    color: rgb(165, 162, 162);
    
}

.newTod {
    padding-top: 20px;
    margin-left: 40px; 
}

.del {
    text-align: end;
}

.fon {

padding-bottom: 300px;
background-image: url("../public/fon2.jpg");
background-repeat: no-repeat;
background-size: 100%;


}

.error {
    color: red;
    /* color :beige */
    
}

.todolist {
    cursor:alias;
    width: 350px;
    display: inline-flex;
    flex-direction: column;
    margin: 20px;
    border-radius: 2%;
    background-color: #f0f4f8;
        
}

.title {
    text-align: center;
    padding-top: 20px;
    padding-bottom: 20px;
}

.input {
    margin: 20px;
}

.li {
    padding-bottom: 10px;
    
}

.buttoncont {
    display: flex;
    justify-content: space-around;
    padding-bottom: 40px;
}

.buttoncont button {
    width: 90px;
}

@media(min-width: 360px) and (max-width: 1160px) {
    .container {
        width: 100%;
        display: flex;
    
    }
    
}